import React, { useState, useEffect } from 'react';
import InputString from 'parts/InputString';
import InputNumber from 'parts/InputNumber';
import SelectItem from 'parts/SelectItem';
import OutputLabel from 'parts/OutputLabel';
import ButtonWithDialog from 'parts/ButtonWithDialog';
import TweetButton from 'parts/TweetButton';
import * as dq1 from 'dq1pswd/dq1pswd';
import * as utils from 'utils/dq1utils';

interface Props {
  password: string;
  setPassword: (password: string) => void;
  moveAnalize: () => void;
}

const herbAndKeyItems: ReadonlyArray<dq1.LabelInfo> = [
  { id: 0, name: '' },
  { id: 1, name: '1' },
  { id: 2, name: '2' },
  { id: 3, name: '3' },
  { id: 4, name: '4' },
  { id: 5, name: '5' },
  { id: 6, name: '6' },
  { id: 7, name: '7', illegal: true },
  { id: 8, name: '8', illegal: true },
  { id: 9, name: '9', illegal: true },
  { id: 10, name: '10', illegal: true },
  { id: 11, name: '11', illegal: true },
  { id: 12, name: '12', illegal: true },
  { id: 13, name: '13', illegal: true },
  { id: 14, name: '14', illegal: true },
  { id: 15, name: '15', illegal: true },
];

const cryptItems: ReadonlyArray<dq1.LabelInfo> = [
  { id: 0, name: '#0' },
  { id: 1, name: '#1' },
  { id: 2, name: '#2' },
  { id: 3, name: '#3' },
  { id: 4, name: '#4' },
  { id: 5, name: '#5' },
  { id: 6, name: '#6' },
  { id: 7, name: '#7' },
];

const equipItems: ReadonlyArray<dq1.LabelInfo> = [
  { id: 0, name: '' },
  { id: 1, name: '' },
];

const slayerItems: ReadonlyArray<dq1.LabelInfo> = [
  { id: 0, name: '' },
  { id: 1, name: '' },
];

const Dq1Edit: React.FC<Props> = (props) => {
  const [showDialog, setShowDialog] = useState<boolean>(false);

  const [name, setName] = useState<string>('');
  const [gold, setGold] = useState<number>(0);
  const [wapon, setWapon] = useState<number>(0);
  const [armor, setArmor] = useState<number>(0);
  const [shild, setShild] = useState<number>(0);
  const [herb, setHerb] = useState<number>(0);
  const [key, setKey] = useState<number>(0);
  const [item1, setItem1] = useState<number>(0);
  const [item2, setItem2] = useState<number>(0);
  const [item3, setItem3] = useState<number>(0);
  const [item4, setItem4] = useState<number>(0);
  const [item5, setItem5] = useState<number>(0);
  const [item6, setItem6] = useState<number>(0);
  const [item7, setItem7] = useState<number>(0);
  const [item8, setItem8] = useState<number>(0);
  const [scale, setScale] = useState<number>(0);
  const [ring, setRing] = useState<number>(0);
  const [amulet, setAmulet] = useState<number>(0);
  const [dragon, setDragon] = useState<number>(0);
  const [golem, setGolem] = useState<number>(0);
  const [exp, setExp] = useState<number>(0);
  const [crypt, setCrypt] = useState<number>(0);
  const [crc, setCrc] = useState<number>(0);

  /** App (/) */
  useEffect(() => {
    if (!props.password) {
      return;
    }
    const info = dq1.analyzePassword(props.password);
    if (info) {
      setName(info.name.replace(/+$/, ''));
      setWapon(info.wapon);
      setArmor(info.armor);
      setShild(info.shild);
      setHerb(info.herb);
      setKey(info.key);
      setItem1(info.items[0]);
      setItem2(info.items[1]);
      setItem3(info.items[2]);
      setItem4(info.items[3]);
      setItem5(info.items[4]);
      setItem6(info.items[5]);
      setItem7(info.items[6]);
      setItem8(info.items[7]);
      setGold(info.gold);
      setExp(info.exp);
      setScale(info.dragonScale ? 1 : 0);
      setRing(info.fighterRing ? 1 : 0);
      setAmulet(info.deadAmulet ? 1 : 0);
      setDragon(info.dragonSlayered ? 1 : 0);
      setGolem(info.golemSlayered ? 1 : 0);
      setCrypt(info.cryptKey);
      setCrc(info.checkCode);
    }
  }, [props.password]);

  /**  */
  const createPassword = () => {
    const info: dq1.Dq1PasswordInfo = {
      name: name,
      wapon: wapon,
      armor: armor,
      shild: shild,
      herb: herb,
      key: key,
      items: [item1, item2, item3, item4, item5, item6, item7, item8],
      gold: gold,
      exp: exp,
      dragonScale: scale === 1,
      fighterRing: ring === 1,
      deadAmulet: amulet === 1,
      dragonSlayered: dragon === 1,
      golemSlayered: golem === 1,
      cryptKey: crypt,
      level: 0, // dummy
      checkCode: 0, // dummy
      valid: false, // dummy
    };
    return dq1.createPassword(info);
  };

  /**  */
  const modalDialog = () => {
    const password = createPassword();
    const info = dq1.analyzePassword(password);
    if (!info) {
      return null;
    }

    return (
      <div id='overlay' onClick={() => setShowDialog(false)}>
        <div className='frame' onClick={(e) => e.stopPropagation()}>
          <h2>
            {info.name} {info.level}
          </h2>
          {info.valid ? (
            <div className='passwd-block'>
              {/* formatter  */}
              
              <br />
              &emsp;&emsp;
              <br />
              <br />
              {dq1
                .editPassword2(password)
                .split('\n')
                .map((it, i) => (
                  <React.Fragment key={`pswd-${i}`}>
                    &emsp;&emsp;{it}
                    <br />
                  </React.Fragment>
                ))}
              <br />
               <br />
            </div>
          ) : (
            <div className='passwd-block'>
              <span className='error'></span>
              <br />
              <br />
              {dq1
                .editPassword2(password)
                .split('\n')
                .map((it, i) => (
                  <React.Fragment key={`pswd-${i}`}>
                    {it}
                    <br />
                  </React.Fragment>
                ))}
            </div>
          )}
          <br />
          <div className='button-area'>
            <div className='button' onClick={() => setShowDialog(false)}>
              
            </div>
            <ButtonWithDialog
              buttonLabel=''
              dialogLabel='Copied!'
              timeout={700}
              onClick={() => void utils.clipboardCopy(dq1.editPassword(password))}
            />
            <TweetButton info={info} />
          </div>
        </div>
      </div>
    );
  };

  return (
    <div>
      <div className='frame'>
        <h2></h2>
        <InputString label='' value={name} setValue={setName} placeholder='' />
        <InputNumber label='' value={exp} setValue={setExp} title='065,535 ' />
        <InputNumber label='' value={gold} setValue={setGold} title='065,535 ' />
      </div>
      <div className='frame'>
        <h2></h2>
        <SelectItem label='' value={wapon} setValue={setWapon} items={dq1.wapons} />
        <SelectItem label='' value={armor} setValue={setArmor} items={dq1.armors} />
        <SelectItem label='' value={shild} setValue={setShild} items={dq1.shilds} />
        <SelectItem label='' value={scale} setValue={setScale} items={equipItems} />
        <SelectItem label='' value={ring} setValue={setRing} items={equipItems} />
        <SelectItem label='' value={amulet} setValue={setAmulet} items={equipItems} />
      </div>
      <div className='frame'>
        <h2></h2>
        <SelectItem label='' value={item1} setValue={setItem1} items={dq1.items} />
        <SelectItem label='' value={item2} setValue={setItem2} items={dq1.items} />
        <SelectItem label='' value={item3} setValue={setItem3} items={dq1.items} />
        <SelectItem label='' value={item4} setValue={setItem4} items={dq1.items} />
        <SelectItem label='' value={item5} setValue={setItem5} items={dq1.items} />
        <SelectItem label='' value={item6} setValue={setItem6} items={dq1.items} />
        <SelectItem label='' value={item7} setValue={setItem7} items={dq1.items} />
        <SelectItem label='' value={item8} setValue={setItem8} items={dq1.items} />
        <SelectItem label='' value={herb} setValue={setHerb} items={herbAndKeyItems} />
        <SelectItem label='' value={key} setValue={setKey} items={herbAndKeyItems} />
      </div>
      <div className='frame'>
        <h2></h2>
        <SelectItem label='' value={dragon} setValue={setDragon} items={slayerItems} />
        <SelectItem label='' value={golem} setValue={setGolem} items={slayerItems} />
      </div>
      <div className='frame'>
        <h2></h2>
        <SelectItem label='' value={crypt} setValue={setCrypt} items={cryptItems} />
        <OutputLabel label='' value={utils.toHex2(crc)} error={crc !== 0} />
      </div>
      <div className='footer'>
        <div className='button-area'>
          <span
            className='button'
            onClick={() => {
              props.setPassword(createPassword());
              props.moveAnalize();
            }}
          >
            
          </span>
          <span
            className='button'
            onClick={() => {
              props.setPassword(createPassword());
              setShowDialog(true);
            }}
          >
            
          </span>
        </div>
      </div>
      {showDialog && modalDialog()}
    </div>
  );
};

export default Dq1Edit;
