import React from 'react';
import OutputLabel from 'parts/OutputLabel';
import ButtonWithDialog from 'parts/ButtonWithDialog';
import TweetButton from 'parts/TweetButton';
import * as dq1 from 'dq1pswd/dq1pswd';
import * as utils from 'utils/dq1utils';

interface Props {
  password: string;
}

const Dq1Info: React.FC<Props> = (props) => {
  const info = dq1.analyzePassword(props.password);
  if (!info) {
    return null;
  }

  return (
    <div>
      <div className='frame'>
        <h2></h2>
        <OutputLabel label='' value={info.name} />
        <OutputLabel label='' value={info.level.toString()} />
        <OutputLabel label='' value={info.exp.toString()} />
        <OutputLabel label='' value={info.gold.toString()} />
        <div className='button-area'>
          <ButtonWithDialog
            buttonLabel=''
            dialogLabel='Copied!'
            timeout={700}
            onClick={() => void utils.clipboardCopy(dq1.editPassword(props.password))}
          />
          <TweetButton info={info} />
        </div>
      </div>
      <div className='frame'>
        <h2></h2>
        <OutputLabel label='' value={dq1.wapons[info.wapon].name} />
        <OutputLabel label='' value={dq1.armors[info.armor].name} />
        <OutputLabel label='' value={dq1.shilds[info.shild].name} />
        <OutputLabel label='' value={info.dragonScale ? '' : ''} />
        <OutputLabel label='' value={info.fighterRing ? '' : ''} />
        <OutputLabel label='' value={info.deadAmulet ? '' : ''} />
      </div>
      <div className='frame'>
        <h2></h2>
        <OutputLabel label='' value={dq1.items[info.items[0]].name} error={!!dq1.items[info.items[0]].illegal} />
        <OutputLabel label='' value={dq1.items[info.items[1]].name} error={!!dq1.items[info.items[1]].illegal} />
        <OutputLabel label='' value={dq1.items[info.items[2]].name} error={!!dq1.items[info.items[2]].illegal} />
        <OutputLabel label='' value={dq1.items[info.items[3]].name} error={!!dq1.items[info.items[3]].illegal} />
        <OutputLabel label='' value={dq1.items[info.items[4]].name} error={!!dq1.items[info.items[4]].illegal} />
        <OutputLabel label='' value={dq1.items[info.items[5]].name} error={!!dq1.items[info.items[5]].illegal} />
        <OutputLabel label='' value={dq1.items[info.items[6]].name} error={!!dq1.items[info.items[6]].illegal} />
        <OutputLabel label='' value={dq1.items[info.items[7]].name} error={!!dq1.items[info.items[7]].illegal} />
        <OutputLabel label='' value={info.herb === 0 ? '' : `${info.herb}`} error={6 < info.herb} />
        <OutputLabel label='' value={info.key === 0 ? '' : `${info.key}`} error={6 < info.key} />
      </div>
      <div className='frame'>
        <h2></h2>
        <OutputLabel label='' value={info.dragonSlayered ? '' : ''} />
        <OutputLabel label='' value={info.golemSlayered ? '' : ''} />
      </div>
      <div className='frame'>
        <h2></h2>
        <OutputLabel label='' value={`#${info.cryptKey}`} />
        <OutputLabel label='' value={utils.toHex2(info.checkCode)} error={info.checkCode !== 0} />
      </div>
    </div>
  );
};

export default Dq1Info;
